import { TCountry } from "../app/CurrencyConverter/interface";

export const qs = <T>(obj: T): string => {
  return Object.entries(obj)
    .map((item) => item.join("="))
    .join("&");
};

export const transformObjectIntoArray = (obj: TCountry): TCountry[] => Object.entries(obj).map(([id, text]) => ({
  id,
  text,
}));
